import React from 'react';
import ReactMarkdown from "react-markdown";

function Index() {
    return (
        <div>
            <ReactMarkdown>## hash、chunkhash、contenthash区别</ReactMarkdown>
            <ReactMarkdown>- 如果是hash的话，是和整个项目有关的，有一处文件发生更改则所有文件的hash值都会发生改变且它们共用一个hash值;</ReactMarkdown>
            <ReactMarkdown>- 如果是chunkhash的话，只和entry的每个入口文件有关，也就是同一个chunk下的文件有所改动该chunk下的文件的hash值就会发生改变;</ReactMarkdown>
            <ReactMarkdown>- 如果是contenthash的话，和每个生成的文件有关，只有当要构建的文件内容发生改变时才会给该文件生成新的hash值，并不会影响其它文件。</ReactMarkdown>
        </div>
    );
}

export default Index;